$font-color: #444;  
$nav-primary: #467a91;
$link-color:$nav-primary;
$primary-lighten-7: lighten($nav-primary, 7%);
$primary-lighten-20: lighten($nav-primary, 20%);
$primary-lighten-30: lighten($nav-primary, 30%);
$primary-lighten-37: lighten($nav-primary, 37%);
$primary-lighten-40: lighten($nav-primary, 40%);
$primary-lighten-45: lighten($nav-primary, 45%);
 
$primary-bg-color: $primary-lighten-45; // for (slideout) menu and panel!!!
 
$navbar-height-dt: 130px; 
$navbar-height-dt: 130px;
$navbar-height-mob: 70px;
$logo-width: 0px;
$z-index-above-all-content: 999;
$breakpoint-xs-sm: 400px;
$breakpoint-sm-md: 700px;
$breakpoint-lg-xl: 1170px; 
@import 'includes/vendor/normalize.min';
@import 'includes/vendor/slideout';
@import 'includes/vendor/metismenu';
@import 'includes/vendor/hamburgers'; 
@import 'includes/mixins';
@import 'includes/navbar'; 
@import 'includes/breadcrumbs';
@import 'includes/default';
@import 'includes/toc';  
@import 'includes/sidebar';

html {
    font-family: 'Rubik', sans-serif; 
    overflow-x: hidden;
    line-height: 1.5;
    color: $font-color;
} 

$min_width: 320px;
$max_width: 1200px;
$min_font: 16px;
$max_font: 50px;

h1{
    @include fluid-type($min_width, $max_width, 35px, 90px);
    margin:50px 0px 10px 0px;
    position: relative; 
}
.default-header{
    line-height: 0.8; 
    padding:25px 25px 25px 25px;
    border-bottom: 5px solid $font-color ;
}
h2{
    @include fluid-type($min_width, $max_width, 20px, 50px);
    margin:30px 0px 10px 0px;
    position: relative;
}
h3{
    @include fluid-type($min_width, $max_width, 19px, 45px);
    margin:25px 0px 10px 0px;
    position: relative;
}
h4{
    @include fluid-type($min_width, $max_width, 18px, 40px);
    margin:20px 0px 10px 0px;
    position: relative;
}
h5{
    @include fluid-type($min_width, $max_width, 17px, 35px);
    margin:15px 0px 10px 0px;
    position: relative;
}
h6{
    @include fluid-type($min_width, $max_width, 16px, 30px);
    margin:10px 0px 8px 0px;
    position: relative;
}

p,li,ul{
    @include fluid-type($min_width, $max_width, 16px, 25px);
}

a{
    @include fluid-type($min_width, $max_width, 16px, 25px);
    text-decoration: none;
    color: $link-color;
    transition: all 0.1s ease-in;
    &:hover{
        color: lighten($link-color, 15%)
    }
}

.hamburger-inner, .hamburger-inner::after, .hamburger-inner::before{
    background-color:#ccc;
}

.small-mobile-noshow {
    @media only screen and (max-width: $breakpoint-xs-sm) {
        display: none!important;
    }
}

.mobile-noshow {
    @media only screen and (max-width: $breakpoint-sm-md) {
        display: none!important;
    }
}

.mobile-hidden {
    @media only screen and (max-width: $breakpoint-sm-md) {
        visibility: hidden!important;
    }
}

.tablet-noshow {
    @media only screen and (max-width: $breakpoint-lg-xl) {
        display: none!important;
    }
}

.desktop-noshow {
    @media only screen and (min-width: $breakpoint-lg-xl) {
        display: none!important;
    }
}

.noshow-above-700{
    @media only screen and (min-width: 700px) {
        display: none!important;
    }
}